.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 4rem;
    padding: 10vh 15vw;
}

.mainText{
    color: #313131;
    font-family: 'Roboto Flex';
    font-size: 3rem;
    transform: scale(1, 1.15);
    text-align: left;
    margin: 0;
}

.experienceContainer {
    display: flex;
    flex-direction: column;
}

.experienceNumber {
    width: 10rem;
}

.experienceText {
    max-width: 9rem;
    font-family: 'Roboto Flex', sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: center;
}