.servicesSection {
    display: flex;
    flex-direction: column;
    padding: 5vh 5vw;
    text-align: center;
    align-items: center;
    gap: 1rem;
  }
  
  .sectionTitle {
    font-size: 4rem;
    margin: 0;
    font-weight: 400;
    line-height: 84.41px;
    text-align: center;


  }
  
  .sectionSubtitle {
    max-width: 80%;
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: center;
  }
  
  .cardContainer {
    justify-content: center;
    display: grid;
    grid-template-columns: 24vw 24vw 24vw;
    gap: 2vw;
  }
  
  .card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid #313131
  }

  .cardContent {
    padding: 1rem;
    text-align: left;
  }
  
  .card:nth-child(5) {
    grid-column: span 2;
    border: 1px solid #e53935;
  }
  
  .cardTitle {
    font-size: 24px;
    font-weight: 400;
    line-height: 31.65px;
    text-align: left;
    margin: 0;
  }
  
  .cardText {
    font-family: 'Roboto Flex';
    font-size: 15px;
    font-weight: 400;
    line-height: 22.5px;
    text-align: left;
  }
  
  .button {
    font-family: 'Roboto Slab';
    font-size: 24px;
    font-weight: 400;
    line-height: 31.65px;
    text-align: left;
    background-color: transparent;
    border: 0;
    outline: 1px solid black;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    padding: 0.5rem;
  }
  
  .button:hover {
    color: white;
    background-color: red;
  }