.aboutSection {
    text-align: center;  /* Light background color */
    padding: 5vh 10vw;
  }
  
  .sectionTitle {
    font-size: 4rem;
    font-weight: 400;
    line-height: 84.41px;
    text-align: center;
  }
  
  .cardContainer {
    width: fit-content;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; /* Allows wrapping to a new row */
    gap: 2vw; /* Space between cards */
    margin: 0 auto; /* Center the container */
    }
  
  .card {
    border: 1px solid #313131; /* Default gray border */
    padding: 1rem;
    text-align: left;
  }
  
  .card:nth-child(1),
  .card:nth-child(4) {
    border: 1px solid #e53935;
    max-width: 20vw; /* Red border for specific cards */
  }

  .card:nth-child(2),
  .card:nth-child(3) {
    width: fit-content;
    max-width: 35vw;;
  }
  
  .cardTitle {
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 31.65px;
    text-align: left;
    margin: 0;
  }
  
  .cardText {
    font-family: 'Roboto Flex';
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: left;

  }
  
  .iconContainer img{
    width: 100%;
  }
  