.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #313131;
    color: white;
    gap: 1rem;
    padding: 5vh 5vw;
}

.contact h2 {
    font-size: 4rem;
    font-weight: 400;
    line-height: 84.41px;
    text-align: center;
    margin-bottom: 0;
}

.contact span {
    font-family: 'Roboto Flex';
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: center;

}

.form {
    min-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 3rem;
    align-items: center;
}

.form input {
    width: 80%;
    color: white;
    background-color: #313131;
    border: none;
    border-bottom: 1px solid white;
    padding: 0.3rem 0.5rem;
}

.form button {
    width: 50%;
    border: 1px solid #ffffff; /* White border */
    padding: 1rem 2rem;
    color: #ffffff;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.4s ease;
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: center;
  }


 .form button:hover {
    background: #F20505;
  }
  
  .form .checkboxContainer {
    display: flex;
    align-items: center;
    color: #696969;
  }
  
  .form .checkbox {
    appearance: none;
    width: 16px;
    height: 16px;
    border: 1px solid #ffffff; /* White border for custom checkbox */
    margin-right: 10px;
    cursor: pointer;
    transition: background-color 0.3s, border-color 0.3s;
  }
  
  .form .checkbox:checked {
    background: #F20505;
  }
  
  
  .form .checkboxLabel {
    font-family: 'Roboto Flex';
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: left;
  }
  

