.header {
    height: 85vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    padding: 5vh 10vw 15vh;
    overflow: hidden;
    background-image: url(../../assets/circle.svg);
    background-repeat: no-repeat; 
}


.button {
    margin-left: auto;
    border: 1px solid #313131;
    padding: 1rem 2rem;
    color: #313131;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.4s ease;
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: center;
  }


  .button:hover {
    color: white;
    background: #F20505;
  }
  
  .titleContainer {
    display: flex;
    flex-direction: column;
  }
  
  .title {
    font-size: 96px;
    font-weight: 400;
    text-align: center;
    color: #313131;
    margin: 0;
  }
  
  .titleHighlight {
    color: #F20505;
  }
  
  .subtitle {
    font-family: 'Roboto Flex', sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 17.58px;
    text-align: center;
  }

  .move {
    border:#313131 solid 1px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
  }
  .move img {
    width: 20px;
    height: 60px;
    position: relative;
    animation: moveArrow 2.5s infinite;
}

/* Keyframes for arrow animation */
@keyframes moveArrow {
    0% {
        top: -40px;
        opacity: 1;
    }
    50% {
        top: 5px; /* Move to the bottom */
        opacity:1;
    }
    100% {
        top: 50px;
        opacity: 1; /* Fades out */
    }
}
  
